import store from 'store'

const STORE_KEY = '__search_history__'
const LENGTH = 10
function check (query) {
  // ,,
  let history = store.get(STORE_KEY)
  if (!history) {
    history = []
  }
  //  ,.
  let index = history.findIndex((item) => {
    return item === query
  })
  if (index === -1) {
    history.unshift(query)
  } else {
    history.splice(index, 1)
    history.unshift(query)
  }
  // ,
  if (history.length > LENGTH) {
    history.pop()
  }
  return history
}
export function getSearchHistory () {
  let result = store.get(STORE_KEY)
  return result
}

export function setSearchHistory (query) {
  let history = check(query)
  store.set(STORE_KEY, history)
  return history
}

export function deleteOneHistory (query) {
  let history = store.get(STORE_KEY)
  let index = history.findIndex((item) => {
    return item === query
  })
  history.splice(index, 1)
  store.set(STORE_KEY, history)
  return history
}

export function emptyHistory (query) {
  let history = []
  store.set(STORE_KEY, history)
  return []
}



